import App from '../app';
const routes = [{
  path: '/',
  key: 'ruleengine',
  component: App,
  indexRoute: { onEnter: (nextState, replace) => replace('/ruleengine/policy/policylist/policyList.htm') },
  childRoutes: [{
    path: 'ruleengine/policy/policylist/policyList.htm',
    key: 'PolicyList',
    chunk: 'PolicyList',
    getComponent: (nextState, cb) => {
      require.ensure([], (require) => {
        cb(null, require('../pages/PolicyList').default)
      }, 'PolicyList')
    },
    title: '',
    onEnter: nextState => {
      if (process.env.BROWSER) {
        const route = nextState.routes.find(item => !!item.title);
        document.title = route.title + '_ - TD Risk Cloud ';
      }
    }
  }, {
    path: 'ruleengine/policy/policyconfig/policyView.htm',
    key: 'PolicyView',
    chunk: 'PolicyView',
    getComponent: (nextState, cb) => {
      require.ensure([], (require) => {
        cb(null, require('../pages/PolicyView').default)
      }, 'PolicyView')
    },
    title: '',
    onEnter: nextState => {
      if (process.env.BROWSER) {
        const route = nextState.routes.find(item => !!item.title);
        document.title = route.title + '_ - TD Risk Cloud ';
      }
    }
  }, {
    path: 'ruleengine/policy/policyconfig/policyHistory.htm',
    key: 'PolicyHistory',
    chunk: 'PolicyHistory',
    getComponent: (nextState, cb) => {
      require.ensure([], (require) => {
        cb(null, require('../pages/PolicyHistory').default)
      }, 'PolicyHistory')
    },
    title: '',
    onEnter: nextState => {
      if (process.env.BROWSER) {
        const route = nextState.routes.find(item => !!item.title);
        document.title = route.title + '_ - TD Risk Cloud ';
      }
    }
  }, {
    path: 'ruleengine/policy/ruleGroup.htm',
    key: 'RuleGroup',
    chunk: 'RuleGroup',
    getComponent: (nextState, cb) => {
      require.ensure([], (require) => {
        cb(null, require('../pages/RuleGroup').default)
      }, 'RuleGroup')
    },
    title: '',
    onEnter: nextState => {
      if (process.env.BROWSER) {
        const route = nextState.routes.find(item => !!item.title);
        document.title = route.title + '_ - TD Risk Cloud ';
      }
    }
  }, {
    path: 'ruleengine/policy/policytemplate/policyTemplateList.htm',
    key: 'PolicyTemplateList',
    chunk: 'PolicyTemplateList',
    getComponent: (nextState, cb) => {
      require.ensure([], (require) => {
        cb(null, require('../pages/PolicyTemplateList').default)
      }, 'PolicyTemplateList')
    },
    title: '',
    onEnter: nextState => {
      if (process.env.BROWSER) {
        const route = nextState.routes.find(item => !!item.title);
        document.title = route.title + '_ - TD Risk Cloud ';
      }
    }
  }, {
    path: 'ruleengine/policy/templateGroup.htm',
    key: 'TemplateGroup',
    chunk: 'TemplateGroup',
    getComponent: (nextState, cb) => {
      require.ensure([], (require) => {
        cb(null, require('../pages/TemplateGroup').default)
      }, 'TemplateGroup')
    },
    title: '',
    onEnter: nextState => {
      if (process.env.BROWSER) {
        const route = nextState.routes.find(item => !!item.title);
        document.title = route.title + '_ - TD Risk Cloud ';
      }
    }
  }]
}];

export default routes;



